.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  height: 100vh;
}

.login__logo {
  width: 6rem;
  object-fit: contain;
  margin: 1.25rem;
}

.login__container {
  border: 1px solid lightgray;
  border-radius: 0.25rem;
  padding: 1.5rem;
  width: 18.75rem;
}

.login__title {
  margin-bottom: 1.25rem;
  font-weight: 500;
}

.login__label {
  display: block;
  font-weight: 700;
  font-size: 0.85rem;
  margin-bottom: 0.25rem;
}

.login__input {
  border: 1px solid darkgray;
  border-radius: 0.15rem;
  width: 95.1%;
  padding: 0.4rem;
  margin-bottom: 0.625rem;
}

.login__input:focus,
.login__input:focus-visible {
  outline: none;
  border-color: #e77600;
  box-shadow: 0 0 3px 2px rgb(228 121 17 / 50%);
}
.login__signInButton{
    background-color: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
}

